/*------------------------------------
  Guidebook
------------------------------------*/

/*------------------------------------
  Variables
------------------------------------*/
$section-padding: 6.25rem;
$card-spacer-y: 20px;
$card-spacer-x: 30px;
$letter-spacing: 0.05em;
$letter-spacing-sm: ($letter-spacing / 2);
$letter-spacing-lg: ($letter-spacing * 2);

/*------------------------------------
  Mixin helpers
------------------------------------*/
@mixin prefix($property, $value, $prefixes: ()) {
  @each $prefix in $prefixes {
    #{'-' + $prefix + '-' + $property}: $value;
  }

  // Output standard non-prefixed declaration
  #{$property}: $value;
}

@mixin text-shadow($size: 25px) {
  text-shadow: 0px 0px $size rgba($black, 0.4);
}

@mixin translate($vertical, $horizontal) {
  @include prefix(transform, translate($vertical, $horizontal), webkit moz);
}

@mixin transition($time: 0.2s, $type: all, $delay: 0s) {
  @include prefix(transition, $type $time, webkit moz);
  @include prefix(transition-delay, $delay, webkit moz);
}

@mixin selection($type) {
  @include prefix(user-select, $type, webkit moz ms);
}

@mixin shadow($size: 15px, $size-2: 0px, $color: rgba($black, 0.05)) {
  @include prefix(box-shadow, 0px 0px $size $size-2 $color, webkit moz);
}

@mixin scale($ratio: 1.05) {
  @include prefix(transform, scale($ratio), webkit moz);
}

@mixin rotate($grade: 90deg) {
  @include prefix(transform, rotate($grade), webkit moz);
}


// remove ?
@mixin no-shadow() {
  @include prefix(box-shadow, none !important, webkit moz);
}


@mixin animation($duration, $mode) {
  @include prefix(animation-duration, $duration, webkit moz);
  @include prefix(animation-fill-mode, $mode, webkit moz);
}

/*------------------------------------
  Boxes
------------------------------------*/

.external,
.code,
.kbd,
.scroll,
.folder,
.file,
.boxed,
.accordion-list .card:hover,
.accordion-list .card.active,
/*
.breadcrumb-back:hover,
.card,
.card-price,
.pagination .page-link:not(.active):hover,
.owl-carousel .owl-nav button.owl-prev,
.owl-carousel .owl-nav button.owl-next,
.tags a:hover,
.figure .figure-caption,
pre[class*="language-"],*/
.map-box {
  -webkit-box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.05);
  -moz-box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.05);
  box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.05);
  border-radius: 0.25rem;
  border: 1px solid #e5e5e5;
  background: white;
}

.bordered {
  border-radius: 0.25rem;
  border: 1px solid #e5e5e5;
}

.no-border {
  border-color: transparent;
}

.boxed>.row {
  margin-left: 0;
  margin-right: 0;
}

.stacked,
.accordion-stack .card {
  -webkit-transition: all 0.2s;
  -moz-transition: all 0.2s;
  transition: all 0.2s;
  -webkit-transition-delay: 0s;
  -moz-transition-delay: 0s;
  transition-delay: 0s;
  position: relative;
  background: $white;
}

.stacked::before,
.accordion-stack .card::before {
  -webkit-transition: all 0.2s;
  -moz-transition: all 0.2s;
  transition: all 0.2s;
  -webkit-transition-delay: 0s;
  -moz-transition-delay: 0s;
  transition-delay: 0s;
  border-radius: 0.25rem;
  opacity: .75;
  content: "";
  position: absolute;
  left: 4px;
  right: 4px;
  bottom: -8px;
  display: block;
  height: 100%;
  background: $white;
  border: 1px solid #e5e5e5;
  z-index: -1;
}

.stacked:hover::before,
.accordion-stack .card:hover::before {
  bottom: -12px;
}

.equal,
.type,
.map-box {
  position: relative;
}

.equal::before,
.type::before,
.map-box::before {
  display: block;
  content: "";
  width: 100%;
  padding-top: 100%;
}

.equal>*,
.type>*,
.map-box>* {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
}

.equal>a,
.type>a,
.map-box>a {
  color: inherit;
}

.equal>a:hover,
.type>a:hover,
.map-box>a:hover {
  color: inherit;
}

.equal[class*="-short"]::before,
.type[class*="-short"]::before,
.map-box[class*="-short"]::before {
  padding-top: 75%;
}

.equal[class*="-long"]::before,
.type[class*="-long"]::before,
.map-box[class*="-long"]::before {
  padding-top: 125%;
}

.lift,
.card.bordered.lift {
  -webkit-transition: all 0.2s;
  -moz-transition: all 0.2s;
  transition: all 0.2s;
  -webkit-transition-delay: 0s;
  -moz-transition-delay: 0s;
  transition-delay: 0s;
}

.lift:hover,
.card.bordered.lift:hover {
  -webkit-transform: translate(0, -10px);
  -moz-transform: translate(0, -10px);
  transform: translate(0, -10px);
  position: relative;
  z-index: 50;
  -webkit-box-shadow: 0px 10px 15px 0 rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 0px 10px 15px 0 rgba(0, 0, 0, 0.1);
  box-shadow: 0px 10px 15px 0 rgba(0, 0, 0, 0.1);
}

.scale {
  -webkit-transition: all 0.2s;
  -moz-transition: all 0.2s;
  transition: all 0.2s;
  -webkit-transition-delay: 0s;
  -moz-transition-delay: 0s;
  transition-delay: 0s;
}

.scale:hover {
  -webkit-transform: scale(1.05);
  -moz-transform: scale(1.05);
  transform: scale(1.05);
  -webkit-box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.05);
  -moz-box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.05);
  box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.05);
  background: $white;
}

.equal .equal-header,
.equal .equal-footer {
  position: absolute;
  right: 0;
  left: 0;
}

.equal .equal-header {
  top: 0;
}

.equal .equal-footer {
  bottom: 0;
}

.equal .image {
  z-index: 0;
}

.equal .row {
  height: 100%;
}

.highlight {
  -webkit-transition: all 0.2s;
  -moz-transition: all 0.2s;
  transition: all 0.2s;
  -webkit-transition-delay: 0s;
  -moz-transition-delay: 0s;
  transition-delay: 0s;
}

.highlight:hover {
  border-color: $primary;
  color: $primary;
}



%boxed {
  @include shadow;
  border-radius: $border-radius;
  border: $border-width solid $border-color;
  background: white;
}

%bordered {
  @include border-radius;
  border: $border-width solid $border-color;
}


.no-border {
  border-color: transparent;
}


// boxed
.boxed {

  @extend %boxed;

  >.row {
    margin-left: 0;
    margin-right: 0;
  }
}

.bordered {
  @extend %bordered;
}

%stacked {
  @include transition;
  position: relative;
  background: $white;

  &::before {
    @include transition;
    @include border-radius;
    opacity: .75;
    content: "";
    position: absolute;
    left: 4px;
    right: 4px;
    bottom: -8px;
    display: block;
    height: 100%;
    background: $white;
    border: $border-width solid $border-color;
    z-index: -1;
  }

  &:hover {
    &::before {
      bottom: -12px;
    }
  }
}

.stacked {
  @extend %stacked;
}


// Equal
//
// equalise width and height of a box
%equal {
  position: relative;
  // overflow: hidden;

  &::before {
    display: block;
    content: "";
    width: 100%;
    padding-top: 100%;
  }

  >* {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
  }

  >a {
    color: inherit;

    &:hover {
      color: inherit;
    }
  }

  &[class*="-short"] {
    &::before {
      padding-top: 75%;
    }
  }

  &[class*="-long"] {
    &::before {
      padding-top: 125%;
    }
  }
}



// lift up
%lift {
  @include transition();

  &:hover {
    @include translate(0, -10px);
    position: relative;
    z-index: 50;
    -webkit-box-shadow: 0px 10px 15px 0 rgba($black, 0.1);
    -moz-box-shadow: 0px 10px 15px 0 rgba($black, 0.1);
    box-shadow: 0px 10px 15px 0 rgba($black, 0.1);
  }
}

.lift {
  @extend %lift;
}

.scale {
  @include transition;

  &:hover {
    @include scale;
    @include shadow;
    background: $white;
  }
}

.equal {
  @extend %equal;

  .equal-header,
  .equal-footer {
    position: absolute;
    right: 0;
    left: 0;
  }

  .equal-header {
    top: 0;
  }

  .equal-footer {
    bottom: 0;
  }

  .image {
    z-index: 0;
  }

  .row {
    height: 100%;
  }
}

%highlight {
  @include transition;

  &:hover {
    border-color: $primary;
    color: $primary;
  }
}

.highlight {
  @extend %highlight;
}



//
// separated items
//
.separated {
  overflow: hidden;

  >* {
    @include prefix(box-shadow, -1px -1px 0px 0px $border-color, webkit moz);

    &:not(:last-child) {
      margin-bottom: 0 !important;
    }
  }
}

.text-white {
  .separated {
    >* {
      @include prefix(box-shadow, -1px -1px 0px 0px rgba($white, .1), webkit moz);
    }
  }
}

/*------------------------------------
  Step-list
------------------------------------*/
.step-list {
  list-style: none;
  margin-top: 40px;
  margin-bottom: 40px;
  padding-left: 55px;

  li {
    position: relative;
    min-height: 40px;
    counter-increment: item;

    &:not(:first-child) {
      margin-top: 20px;
    }

    &::before {
      @include translate(0, -50%);
      position: absolute;
      top: .9em;
      left: -55px;
      content: counter(item);
      color: $white;
      font-weight: 600;
      line-height: 36px;
      text-align: center;
      display: block;
      width: 40px;
      height: 40px;
      background: $secondary;
      border-radius: 50%;
      border: 2px solid $secondary;
    }

    li {

      &::before {
        color: $secondary;
        background: transparent;
        border-color: $secondary;
      }

      &:not(:first-child) {
        margin-top: .5rem;
      }
    }
  }

  ul,
  ol {
    padding-left: 45px;
    margin-top: 20px;
    margin-bottom: 20px;
    list-style: none;
    counter-reset: item;
  }
}

/*------------------------------------
  External, folder, file, code and kbd
------------------------------------*/

.external,
.code,
.kbd,
.scroll,
.folder,
.file {
  -webkit-transition: all 0.2s;
  -moz-transition: all 0.2s;
  transition: all 0.2s;
  -webkit-transition-delay: 0s;
  -moz-transition-delay: 0s;
  transition-delay: 0s;
  position: relative;
  display: inline-block;
  margin: .2rem;
  padding: 0 .5rem 0 2.5rem;
  font-size: inherit;
  font-family: "Open Sans", sans-serif;
  border-radius: 0.25rem;
  color: #555555;
}

.external::before,
.code::before,
.kbd::before,
.scroll::before,
.folder::before,
.file::before {
  -webkit-transition: all 0.2s;
  -moz-transition: all 0.2s;
  transition: all 0.2s;
  -webkit-transition-delay: 0s;
  -moz-transition-delay: 0s;
  transition-delay: 0s;
  -webkit-transform: translate(0, -50%);
  -moz-transform: translate(0, -50%);
  transform: translate(0, -50%);
  position: absolute;
  left: 0;
  top: 50%;
  display: inline-block;
  width: 2rem;
  height: 100%;
  text-align: center;
  background: #f5f5f5;
  content: "\f0c1";
  color: #000;
  font-family: 'Line Awesome Free' !important;
  border-radius: 0.25rem;
  border-right: 1px solid #e5e5e5;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  font-weight: 600;
}

.text-white .external,
.text-white .code,
.text-white .kbd,
.text-white .scroll,
.text-white .folder,
.text-white .file {
  border-color: rgba(255, 255, 255, 0.1);
  background: rgba(0, 0, 0, 0.2);
  color: $white;
}

.text-white .external:before,
.text-white .code:before,
.text-white .kbd:before,
.text-white .scroll:before,
.text-white .folder:before,
.text-white .file:before {
  background: rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.5);
  border-color: transparent;
}

.external::before {
  content: "\f35d";
}

.external:hover {
  background: $primary;
  color: $white;
  border-color: $primary;
}

.external:hover::before {
  background: rgba(0, 0, 0, 0.1);
  color: $white;
  border-color: rgba(0, 0, 0, 0.1);
}

.code::before {
  content: "\f121";
}

.folder::before {
  content: "\f07c";
}

.file::before {
  content: "\f15b";
}

.kbd::before {
  content: "\f11c";
}

/*------------------------------------
  Accordion
------------------------------------*/
.accordion {

  .card {
    @include transition;
    overflow: visible;

    .card-header {
      padding: 0;
      border: 0;
      background: transparent;

      >* {
        font-family: $font-family-sans-serif;
        font-weight: $font-weight-normal;
      }

      button {
        @include transition;
        position: relative;
        width: 100%;
        padding: $card-spacer-y $card-spacer-x;
        text-align: left;
        text-transform: none;
        font-weight: $font-weight-normal;
        font-size: 1rem;
        letter-spacing: 0;
        color: inherit;
        white-space: normal;

        &:hover {
          text-decoration: none;
        }

        &::before {
          position: absolute;
          content: "\f107";
          width: 1em;
          line-height: 1em;
          right: $card-spacer-x;
          top: 50%;
          font-family: 'Line Awesome Free' !important;
          font-size: 1.5rem;
          font-weight: 600;
          margin-top: -0.5em;
          color: $secondary;
          @include transition(0.2s);
        }

        &[aria-expanded="true"] {
          &::before {
            @include rotate(180deg);
          }
        }
      }
    }

    .card-body {
      padding-top: 0;
    }
  }

}


.accordion-boxed {
  @include shadow;

  .card {
    @include no-shadow;

    .card-header {
      button {
        padding: $card-spacer-y ($card-spacer-x * 2) $card-spacer-y $card-spacer-x;
      }
    }
  }
}

.accordion-bordered {
  @include no-shadow;
  background: transparent;

  .card {
    @include no-shadow;
    background: transparent;

    .card-header {
      button {
        padding: $card-spacer-y ($card-spacer-x * 2) $card-spacer-y $card-spacer-x;
      }
    }
  }
}


// fluid
.accordion-fluid {

  .card {
    @include no-shadow;
    @include transition;
    border-color: transparent;
    background: transparent;

    &::before,
    &::after {
      @include transition;
      content: "";
      position: absolute;
      display: block;
      top: -1px;
      bottom: -$border-width;
      width: 100vw;
    }

    &::before {
      right: 100%;
    }

    &::after {
      left: 100%;
    }

    &:not(:first-of-type):not(:last-of-type),
    &:first-of-type {
      border-bottom: $border-width solid $border-color;

      &::before,
      &::after {
        border-bottom: $border-width solid $border-color;
      }
    }

    .card-header {
      button {
        padding: ($section-padding / 2) ($card-spacer-x * 2) ($section-padding / 2) 0;
        font-size: 1.25rem;
      }
    }

    .card-body {
      padding: 0 0 ($card-spacer-x * 2) 0;
    }

    &.active {
      background: $white;

      &::before,
      &::after {
        background: $white;
      }
    }
  }

}


// stacks
.accordion-stack {
  @include no-shadow;
  background: transparent;

  .card {
    @extend %stacked;

    &.active {
      &::before {
        bottom: 0;
      }
    }

    &:not(:first-of-type):not(:last-of-type),
    &:first-of-type {
      @include border-radius;
      border-bottom: $border-width solid $border-color;
    }

    &:last-of-type {
      @include border-radius;
    }

    .card-header {

      button {
        position: relative;
        width: 100%;
        text-align: left;
        font-size: $font-size-lg;

        &::before {
          position: static;
          content: "\f107";
          display: inline-flex;
          align-items: center;
          justify-content: center;
          vertical-align: middle;
          width: 2.5rem;
          height: 2.5rem;
          margin-right: $card-spacer-y;
          border-radius: 50%;
          font-family: 'Line Awesome Free' !important;
          margin-top: 0;
          background: $primary;
          color: $white;
        }

        &[aria-expanded="true"] {
          &::before {
            content: "\f106";
            @include rotate(0deg);
          }
        }
      }
    }

    +.card {
      margin-top: ($card-spacer-x / 2);
    }
  }
}


// changelog
.accordion-changelog {
  .card {
    .card-header {
      button {
        padding: $card-spacer-x ($card-spacer-x * 2 + 10px);
      }
    }

    &:hover {
      .label {
        @include scale(1.5);
      }
    }

    &:last-of-type {
      border-bottom: $border-width solid $border-color;
      border-bottom-left-radius: $border-radius;
      border-bottom-right-radius: $border-radius;
    }
  }

  .label {
    @include transition;
    position: absolute;
    top: 50%;
    left: $card-spacer-x;
    display: block;
    width: 10px;
    height: 10px;
    margin-top: -5px;
    border-radius: 50%;
    background: $gray-100;
  }

}

/* Accordion list */
.accordion-list {
  background: transparent;

  .card {
    @include transition;
    box-shadow: none;
    border-radius: $border-radius;
    padding-left: 3.125rem;
    background: transparent;
    border-color: transparent;
    counter-increment: accordion;

    &:not(:first-of-type):not(:last-of-type),
    &:first-of-type {
      border-bottom: $border-width solid transparent;
      border-radius: $border-radius;
    }

    &:not(:first-child) {
      margin-top: 10px;
    }

    &:hover {
      @extend %boxed;

      &:not(:first-of-type):not(:last-of-type),
      &:first-of-type {
        border-bottom-color: $border-color;
      }
    }

    /*&::before {
      @include translate(0, -50%);
      @include transition;
      content: counter(accordion, decimal);
      position: absolute;
      display: block;
      top: 50%;
      left: 10px;
      width: 3.125rem;
      height: 3.125rem;
      color: $white;
      line-height: 3.125rem;
      text-align: center;
      z-index: 20;
      border-radius: $border-radius;
      background: $secondary;
      font-size: $font-size-base;
      font-weight: 600;
    }

    &.active {
      @extend %boxed;

      &:not(:first-of-type):not(:last-of-type),
      &:first-of-type {
        border-bottom-color: $border-color;
      }

      &::before {
        @include translate(-20px, -50%);
        background: $primary;
        color: $white;
      }
    }*/

    [data-bs-target].btn::before {
      //@include translate(0, -50%);
      @include transition;
      content: counter(accordion, decimal);
      position: absolute;
      display: block;
      top: 30%;
      left: -40px;
      width: 3.125rem;
      height: 3.125rem;
      color: $white;
      line-height: 3.125rem;
      text-align: center;
      z-index: 20;
      border-radius: $border-radius;
      background: $secondary;
      font-size: $font-size-base;
      font-weight: 600;
    }
    
    .card-header button[aria-expanded="true"]::before {
      transform: rotate(0deg);
    }
    
    [data-bs-target]:not(.collapsed), .collapse.show {
      background: $white;
      border: none;

      &:not(:first-of-type):not(:last-of-type),
      &:first-of-type {
        border-bottom-color: $border-color;
      }

      &::before {
        transform: none;
        background: $primary;
        color: $white;
      }
    }

    .card-header {
      button {
        line-height: 30px;
        padding-top: 20px;
        padding-bottom: 20px;

        &::before {
          display: none;
        }
      }
    }
  }
}

/*------------------------------------
  Doc
------------------------------------*/

.doc-sidebar {
  padding: $card-spacer-x;
  border-right: $border-width solid $border-color;
}

.doc-content-header {
  margin-left: -($card-spacer-x / 2);
  margin-right: -($card-spacer-x / 2);
  margin-bottom: $grid-gutter-width;
  padding: $grid-gutter-width / 2;
  border-bottom: $border-width solid $border-color;

  @include media-breakpoint-up(lg) {
    margin-bottom: 0;
    padding: $card-spacer-x 3.125rem;
  }

  @include media-breakpoint-down(md) {
    border-top: $border-width solid $border-color;
    background: $white;
  }

  .breadcrumb {
    margin: 0;
    padding: 0;
    border: 0;
  }
}


.doc-content-body {
  margin-top: 0 !important;

  >article {
    padding: $grid-gutter-width / 2;
  }

  >aside {
    padding: $grid-gutter-width / 2;
    border-left: $border-width solid $border-color;
  }

  @include media-breakpoint-up(lg) {
    >article {
      padding: 3.125rem;
    }

    >aside {
      padding: 3.125rem $card-spacer-x;
    }
  }

  .toc {
    margin-left: -$card-spacer-x;
    margin-right: -$card-spacer-x;
  }
}

/*------------------------------------
  Toc
------------------------------------*/
.toc {

  .nav-link {
    @include transition;
    position: relative;
    width: 100%;
    color: $gray-600;
    font-size: $font-size-sm;
    border-left: 2px solid transparent;
    padding: .5rem $card-spacer-x;

    &:hover {
      color: $black;
      border-color: $gray-500;
    }

    &.active {
      color: $primary;
      border-color: $primary;

      +ul {
        display: block;
      }
    }
  }

  ul {
    border-left: 2px solid $primary;
    display: none;
    padding-left: $card-spacer-x;

    .nav-link {
      font-size: $font-size-sm;
      border-color: $border-color;
    }
  }

}

// tags
.tags {
  margin: -.25rem;
  overflow: hidden;

  a {
    @include transition;
    @include shadow(0, $border-width, $border-color);
    float: left;
    display: block;
    margin: .25rem;
    padding: .5rem .75rem;
    font-size: $font-size-sm;
    color: $gray-700;
    border-radius: $border-radius;

    &:hover {
      @extend %boxed;
      border: 0;
    }
  }
}


/*------------------------------------
  Numbers
------------------------------------*/
.number {
  text-align: center;

  span {
    font-size: 3rem;
    text-transform: uppercase;
    color: $black;
  }

  p {
    font-size: $font-size-sm;
  }

  @include media-breakpoint-down(sm) {
    padding-top: $card-spacer-x;
    padding-bottom: $card-spacer-x;
  }
}


/*------------------------------------
  Type
------------------------------------*/
.type {
  @extend %equal;

  >* {
    padding: $card-spacer-x;

    @include media-breakpoint-down(sm) {
      padding: $card-spacer-y;
    }
  }

  .badge {
    position: absolute;
    top: $card-spacer-y;
    right: -$card-spacer-y;
  }
}

.type-title {
  font-family: $font-family-sans-serif;
  font-weight: $font-weight-normal;
  font-size: $font-size-sm;
  color: $body-color;
}

.type-example {
  position: absolute;
  left: $card-spacer-x;
  bottom: $card-spacer-x;

  @include media-breakpoint-down(sm) {
    left: $card-spacer-y;
    bottom: $card-spacer-y;
  }

  span {
    font-size: 3.125rem;
    line-height: 1em;
    color: $black;
  }
}


/*------------------------------------
  Color
------------------------------------*/
.color {
  display: flex;
  align-items: center;
  padding: 10px;
  border-radius: 10rem;

  .color-example {
    width: 80px;
    height: 80px;
    margin-right: 20px;
    border-radius: 50%;
  }

  .color-info {
    flex: 1;

    span {
      font-size: $font-size-sm;
      color: $gray-600;
      text-transform: uppercase;
    }
  }

  .color-title {
    font-family: $font-family-sans-serif;
    font-size: $font-size-lg;
    font-weight: 500;
    margin-bottom: 0;
  }
}


/*------------------------------------
  Page meta
------------------------------------*/
.page-meta-stat {
  display: flex;
  justify-content: center;
  align-items: center;

  b {
    color: $white;
    margin-right: .625rem;
    font-weight: 700;
  }

  span {
    font-size: $font-size-sm;
    text-transform: uppercase;
    letter-spacing: $letter-spacing;
    color: rgba($white, .75);
  }
}


/*------------------------------------
  File tree
------------------------------------*/
.file-tree {
  margin-bottom: 0;
  padding-left: 20px;
  list-style: none;
  overflow: hidden;

  ul {
    list-style: none;
    display: none;
    padding-left: $card-spacer-x;
  }

  li {
    position: relative;
    padding: 5px 0;
    border-radius: $border-radius;
    cursor: auto;
    white-space: nowrap;
    font-size: $font-size-sm;
    font-weight: 600;

    &::before {
      content: "\f15b";
      display: inline-block;
      text-align: center;
      width: $card-spacer-x;
      margin-right: .25rem;
      vertical-align: middle;
      font-family: 'Line Awesome Free' !important;
      font-size: 1rem;
      color: $secondary;
    }

    span {
      color: $gray-600;
      font-weight: $font-weight-normal;
      font-size: .75rem;
    }
  }

  .file-tree-folder {
    @include transition;
    cursor: pointer;

    &::before {
      content: "\f07b";
      font-size: 1.25rem;
    }

    &.open {
      &::after {
        @include rotate(180deg);
      }

      >ul {
        display: block;
      }
    }

    &::after {
      @include transition;
      position: absolute;
      top: .8rem;
      left: -1em;
      content: "\f107";
      display: block;
      text-align: center;
      width: 1em;
      line-height: 1em;
      font-family: 'Line Awesome Free' !important;
      font-size: 1rem;
      color: $secondary;
    }

    &.open {
      &::before {
        content: "\f07c";
      }
    }

    &.empty {
      &::after {
        display: none;
      }
    }
  }
}
