// Variables
//
// Variables should follow the `$component-state-property-size` formula for
// consistent naming. Ex: $nav-link-disabled-color and $modal-content-box-shadow-xs.

// Color system

// Shades of grey
$white   : #ffffff;
$gray-50:  #F9FAFB;
$gray-100: #F2F4F6;
$gray-200: #E5E7EB;
$gray-300: #D1D5DB;
$gray-400: #9CA3AF;
$gray-500: #6B7280;
$gray-600: #4B5563;
$gray-700: #374151;
$gray-800: #1F2937;
$gray-900: #111827;
$dark    : #0d1821;
$black   : #222222;

// fusv-disable
$grays: ("100": $gray-100,
    "200": $gray-200,
    "300": $gray-300,
    "400": $gray-400,
    "500": $gray-500,
    "600": $gray-600,
    "700": $gray-700,
    "800": $gray-800,
    "900": $gray-900);
// fusv-enable

// Generic colors
$blue       : #2361ce;
$indigo     : #4F46E5;
$purple     : #7C3AED;
$pink       : #EF4683;
$red        : #E11D48;
$orange     : #FBA918;
$brown      : #b9a084;
$yellow     : #f3c78e;
$yellow-100 : #ffeed6;
$yellow-200 : #f5dbb8;
$blue-100   : #cfe2ff;
$blue-200   : #9ec5fe;
$green      : #10B981;
$teal       : #1E90FF;
$cyan       : #63b1bd;
$soft-indigo: #f5e8ff;
$soft-green : #2CA58D;

// scss-docs-start colors-map
$colors: ("blue": $blue,
    "indigo": $indigo,
    "purple": $purple,
    "pink": $pink,
    "red": $red,
    "orange": $orange,
    "yellow": $yellow,
    "green": $green,
    "teal": $teal,
    "cyan": $cyan,
    "white": $white,
    "gray": $gray-600,
    "gray-dark": $gray-800);
// scss-docs-end colors-map

// Color scheme
$primary  : #002c73; //#ffd009 #1274ed #002c73
$secondary: #1274ed;
$tertiary : #ffd009;

// Series names and colors. This can be extended or customized as desired. Just add more series and colors.
$ct-series-names: (a, b, c, d, e, f, g);
$ct-series-colors: (#262B40,
    #F8BD7A,
    #2CA58D,
    #31316A,
    #C96480,
    #ffffff,
    #F8BD7A,

);


$success: $green;
$info   : $blue;
$warning: $yellow;
$danger : $red;
$gray   : $gray-900;
$light  : $gray-400;
$lighten: $gray-300;
$soft   : $gray-200;
$dark   : $dark;

// Brands colors
$facebook : #3b5999;
$twitter  : #1da1f2;
$google   : #DB4337;
$instagram: #e4405f;
$pinterest: #bd081c;
$youtube  : #cd201f;
$slack    : #3aaf85;
$dribbble : #ea4c89;
$github   : #222222;
$dropbox  : #1E90FF;
$twitch   : #4B367C;
$paypal   : #ecb32c;
$behance  : #0057ff;
$reddit   : #E84422;

// Brand colors
$brand-colors: ("facebook": $facebook,
    "twitter": $twitter,
    "google": $google,
    "instagram": $instagram,
    "pinterest": $pinterest,
    "youtube": $youtube,
    "slack": $slack,
    "dribbble": $dribbble,
    "dropbox": $dropbox,
    "twitch": $twitch,
    "paypal": $paypal,
    "behance": $behance,
    "reddit" : $reddit,
    "github": $github);

// Theme colors
$theme-colors: (
    "primary": $primary,
    "secondary": $secondary,
    "tertiary": $tertiary,
    "success": $success,
    "info": $info,
    "warning": $warning,
    "danger": $danger,
    "white": $white,
    "indigo": $indigo,
    "purple": $purple,
    "dark": $dark,
    "light": $light,
    "yellow-100": $yellow-100,
    "yellow-200": $yellow-200,
    "blue-100": $blue-100,
    "blue-200": $blue-200,
    "gray-50": $gray-50,
    "gray-100": $gray-100,
    "gray-200": $gray-200,
    "gray-300": $gray-300,
    "gray-400": $gray-400,
    "gray-500": $gray-500,
    "gray-600": $gray-600,
    "gray-700": $gray-700,
    "gray-800": $gray-800,
    "gray-900": $gray-900
);

    // Navbar colors
    $navbar-colors: ("primary": $primary, "secondary": $secondary, "dark": $dark, "tertiary": $tertiary);

// Body
//
// Settings for the `<body>` element.

$body-bg        : $gray-100;
$body-color     : $dark;
$body-text-align: null;


// Links
//
// Style anchor elements.

$link-color                             : $primary;
$link-decoration                        : none;
$link-hover-color                       : darken($link-color, 50%);
$link-hover-decoration                  : none;
// Darken percentage for links with `.text-*` class (e.g. `.text-success`)
$emphasized-link-hover-darken-percentage: 10%;

$stretched-link-pseudo-element: after;
$stretched-link-z-index       : 1;

// Components
//
// Define common padding and border radius sizes and more.

$border-width      : .0625rem;
$border-width-md   : 0.125rem;
$border-width-lg   : 0.25rem;
$border-width-xl   : 0.375rem;
$border-color-white: $white;
$border-color      : $gray-200;


$border-radius     : .4rem;
$border-radius-sm  : .25rem;
$border-radius-lg  : 1rem;
$border-radius-xl  : 1.5rem;
$border-radius-pill: 50rem;
$circle-radius     : 50%;

$component-active-color       : $white;
$component-active-bg          : $primary;
$component-active-border-color: $primary;

$component-hover-color       : $gray-300;
$component-hover-bg          : $gray-300;
$component-hover-border-color: $gray-300;

$rounded-pill: 50rem;

$box-shadow      : inherit;
$box-shadow-sm   : inherit;
$box-shadow-lg   : inherit;
$box-shadow-inset: inherit;

$component-active-color: $white;
$component-active-bg   : $primary;

$caret-width         : .3em;
$caret-vertical-align: $caret-width * .85;
$caret-spacing       : $caret-width * .85;

$transition-base     : all .2s ease;
$transition-transform: transform .2s ease;
$transition-fade     : opacity .15s linear;
$transition-collapse : height .35s ease;
$transition-tabs     : all 0.2s;

// scss-docs-start embed-responsive-aspect-ratios
$embed-responsive-aspect-ratios: ("21by9": (x: 21,
        y: 9),
    "16by9": (x: 16,
        y: 9),
    "4by3": (x: 4,
        y: 3),
    "1by1": (x: 1,
        y: 1));
// scss-docs-end embed-responsive-aspect-ratios

// Buttons
//
// For each of Bootstrap's buttons, define text, background, and border color.

$btn-box-shadow : $box-shadow;
$btn-disabled-opacity : .65;
$btn-active-box-shadow: null;

$btn-link-color         : $link-color;
$btn-link-hover-color   : $link-hover-color;
$btn-link-disabled-color: $gray-600;

$btn-block-spacing-y: .5rem;

// Allows for customizing button radius independently from global border radius
$btn-border-radius   : 0rem;
$btn-border-radius-sm: 0rem;
$btn-border-radius-lg: 0rem;

// Navbar

$navbar-light-color: $dark;
$navbar-light-hover-color: $secondary;
$navbar-light-active-color: $primary;